import React , {Component} from 'react' ; 
import Card from 'react-bootstrap/lib/Card';

class ProductRow extends Component {
    render() {
        const product = this.props.product ; 
        const name = product.cv ? 
            'Card_CV':'text-center';
            
        return (
                <Card className={name} >
                    <Card.Img variant="top" className='img-fluid' src={`img/${product.imagen}`} />
                    <Card.Body>
                        <Card.Title className="hola">{product.name}</Card.Title>
                        <Card.Text className="hola">
                            {product.price}
                        </Card.Text>
                    </Card.Body>
                    </Card>                             
        );
    }
}

export default ProductRow ;