import React, { Component } from "react";
import  {Redirect} from 'react-router';
import Scanner from './Scanner';
import Button from 'react-bootstrap/lib/Button' ; 
import PropTypes from 'prop-types' ;

class Contact extends Component {
    constructor(props){
      super(props);        
      this.state = { 
        scanning: false,
        results : 0
      };
    
      this._scan= this._scan.bind(this);
      this._onDetected = this._onDetected.bind(this);

    }
    someFn = () => {
        let listInfo = this.state.results ;
        this.props.callbackFromParent(listInfo);
    }
  render() {
      const largo = (this.state.results !== 0);
      const obj = largo ? <Redirect to={{ pathname: "/" }}/>
      : '' ;
      if (largo) {
          this.someFn() ;
      }

    return (
        <div>
            <Button onClick={this._scan}   variant="primary" size="lg">{this.state.scanning ? 'Stop' : 'Start'}</Button>
            {obj}
            {this.state.scanning ? <Scanner onDetected={this._onDetected}/> : null}
        </div>
    );
}


_scan() {
    this.setState({scanning: !this.state.scanning});
}

_onDetected(result) {
    let result1= PropTypes.object;
    result1= result ;
    this.setState({results: result1.codeResult.code});
    }

};
 
export default Contact;